.store {
    margin: 0 auto;
    width: 100%;
    height: 100px;

    &_headerBack {
        padding: var(--content-padding) var(--content-padding);
        width: calc(100% - calc(var(--content-padding) * 2));
        height: 50px;
        background-color: var(--second-color);

        &_header {
            margin: 0 auto;
            width: 100%;
            height: 50px;
            max-width: var(--content-width);
            display: flex;
            align-items: center;
            justify-content: space-between;
            column-gap: 10px;

            &_title {
                font-family: inter;
                color: var(--main-color);
                font-weight: 600;
                font-size: 2em;
            }

            &_search {
                width: 400px;
                height: 40px;
                display: flex;

                &_input {
                    width: 100%;
                    padding-left: 8px;
                    border: 1px solid var(--main-color);
                    border-right: none;
                    outline: none;
                    font-family: inter;
                    font-size: 1em;

                }

                &_btn {
                    height: 100%;
                    aspect-ratio: 1/1;
                    border: 1px solid var(--main-color);
                    background-color: var(--main-color);
                    cursor: pointer;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    &_img {
                        height: 50%;
                    }
                }
            }
        }
    }

    &_products {
        width: 100%;
        background-color: var(--main-color);

        &_place {
            margin: 0 auto;
            padding: var(--content-padding) var(--content-padding);
            width: calc(100% - calc(var(--content-padding) * 2));
            max-width: var(--content-width);
            display: flex;
            justify-content: space-evenly;
            column-gap: 95px;
            flex-wrap: wrap;
            row-gap: 30px;

            &_item {
                position: relative;
                height: 373px;
                width: 370px;

                &_panel {
                    position: absolute;
                    bottom: 0;
                    height: calc(50% - 30px);
                    width: calc(100% - calc(20px * 2));
                    padding: 20px;
                    background-color: rgba(0, 0, 0, 0.493);
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    align-items: self-start;

                    &_p1 {
                        font-family: inter;
                        font-weight: 500;
                        color: var(--second-color);
                        font-size: 1.4em;
                    }

                    &_p2 {
                        font-family: inter;
                        font-weight: 500;
                        color: var(--main-color-light);
                        font-size: 1em;
                    }

                    &_btn {
                        width: auto;
                        display: block;
                        padding: 10px 30px;
                        border-radius: 6px;
                        font-family: inter;
                        background-color: rgba(255, 255, 255, 0.116);
                        border: none;
                        color: var(--second-color);
                        cursor: pointer;
                        transition-duration: 0.2s;

                        &:hover {
                            background-color: #fff;
                            color: var(--main-color);
                        }
                    }

                }

                &_img {
                    width: 100%;
                    height: 373px;
                    position: absolute;
                    object-fit: cover;
                }
            }
        }
    }
}





@media screen and (max-width: 1380px) {
    .store {
        &_products {
            &_place {
                &_item {
                    width: 40%;
                    min-width: 320px;
                }
            }
        }
    }
}

@media screen and (max-width: 815px) {
    .store {
        &_products {
            &_place {
                &_item {
                    width: 100%;
                    max-width: 500px;
                    min-width: 280px;
                }
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .store {
        &_headerBack {

            &_header {
                margin: 0 auto;
                width: 100%;
                height: 40px;
                max-width: var(--content-width);
                display: flex;
                align-items: center;
                justify-content: space-between;
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .store {
        &_headerBack {
            height: auto;

            &_header {
                margin: 0 auto;
                width: 100%;
                height: auto;
                max-width: var(--content-width);
                display: flex;
                align-items: center;
                justify-content: space-between;
                flex-direction: column;
                row-gap: 20px;

                &_search {
                    width: 100%;
                }
            }
        }
    }
}