.navigation-enter {
    transform: translateY(-100%);
}

.navigation-enter-active {
    transform: translateY(0px);
    transition: 300ms ease-in-out;
    /* Adjust timing function and duration */
}

.navigation-exit {
    transform: translateY(0px);
}

.navigation-exit-active {
    transform: translateY(-100%);
    transition: 300ms ease-in-out;
    /* Adjust timing function and duration */
}