.poster {
    width: 100%;
    padding: 75px 0;
    background-image: url(../../../features/images/default/poster.png);
    background-repeat: no-repeat;
    background-size: cover;

    &_content {
        margin: 0 auto;
        width: calc(100% - calc(var(--content-padding) * 2));
        padding: 0 var(--content-padding);
        max-width: var(--content-width);
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        row-gap: 30px;

        &_title {
            font-family: josefin;
            color: var(--second-color);
            font-size: 3em;
            font-weight: 500;
        }

        &_text {
            font-family: inter;
            font-weight: 500;
            text-align: center;
            color: var(--main-color-light);
            font-size: 1.4em;
        }

        &_actions {
            width: 100%;
            height: var(--btn-height);

            &_btn {
                height: 100%;
            }
        }
    }
}