.choosing {
    margin: 0 auto;
    width: calc(100% - calc(var(--content-padding) * 2));
    padding: 0 var(--content-padding);
    max-width: var(--content-width);
    display: flex;
    justify-content: space-between;
    column-gap: var(--content-padding);
    height: 400px;

    &_details {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &_texts {
            width: 100%;
            display: flex;
            flex-direction: column;
            row-gap: 20px;

            &_title {
                font-family: inter;
                color: var(--main-color);
                font-weight: 600;
                font-size: 2em;
            }

            &_text {
                font-family: inter;
                color: var(--main-color-light);
                font-size: 1.4em;
            }
        }

        &_actions {
            width: 100%;
            height: var(--btn-height);
            flex-shrink: 0;

            &_btn {
                height: 100%;
            }
        }
    }


}

@media screen and (max-width: 768px) {
    .choosing {
        height: 300px;

        &_img {
            height: 300px;
            aspect-ratio: 3/4;
        }
    }
}

@media screen and (max-width: 500px) {
    .choosing {
        height: auto;
        flex-direction: column;
        row-gap: var(--content-padding);

        &_img {
            width: 100%;
            height: 200px;
            aspect-ratio: 1/2;
            object-fit: cover;
        }

        &_details {
            row-gap: 10px;

            &_actions {
                &_btn {
                    height: 30px;
                }
            }
        }
    }
}