.btn {
    width: auto;
    padding: 0 30px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--second-color);
    background-color: var(--additional-color);
    border: none;
    border-radius: 10px;
    font-family: inter;
    transition-duration: 0.3s;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    word-wrap: none;
    text-wrap: nowrap;
    font-size: 1em;

    &:hover {
        &::after {
            opacity: 1;
        }
    }

    &::after {
        transition-duration: 0.2s;
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(255, 255, 255, 0.205);
        opacity: 0;
    }
}