:root {
    --local-header-height: 60px;
}

.header {
    width: calc(100% - calc(var(--content-padding) * 2));
    padding: 0 var(--content-padding);
    background-color: var(--second-color);
    height: var(--local-header-height);
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    z-index: 2;

    &_logo {
        font-family: inter;
        font-weight: 600;
        font-size: 1.5em;
        font-style: italic;
    }

    &_nav {
        display: flex;
        align-items: center;
        justify-content: center;
        column-gap: 30px;

        &_item {
            font-family: inter;
            color: var(--main-color);
        }
    }

    &_btn {
        height: var(--btn-height);
    }

    &_window {
        position: absolute;
        top: var(--local-header-height);
        left: 0;
        width: calc(100% - calc(var(--content-padding) * 2));
        padding: 10px var(--content-padding) 10px var(--content-padding);

        background-color: var(--second-color);
        display: flex;
        flex-direction: column;
        align-items: center;

        &_panel {
            width: 100%;
            height: 30px;
            display: flex;
            justify-content: space-between;
            align-items: center;

            &_title {
                font-family: inter;
                font-size: 1.2em;
                color: var(--main-color);
            }

            &_close {
                height: 70%;
                aspect-ratio: 1/1;
                cursor: pointer;
            }
        }

        &_item {
            height: 40px;
            width: 100%;
            border-top: 1px solid var(--main-color);
            display: flex;
            justify-content: center;
            align-items: center;
            font-family: inter;
            font-size: 1em;
            color: var(--main-color);

            &_first {
                border-top: none;
            }
        }

        &_btn {
            width: 100%;
            height: var(--btn-height);
        }
    }

    &_menu {

        height: 70%;
        aspect-ratio: 1/1;
        display: none;
        flex-direction: column;
        justify-content: space-evenly;

        &_line {
            width: 100%;
            height: 3px;
            background-color: black;
            border-radius: 2px;
        }
    }
}

@media screen and (max-width: 768px) {
    :root {
        --local-header-height: 40px;
    }

    .header {

        &_btn {
            height: 70%;
        }
    }
}

@media screen and (max-width: 425px) {
    .header {
        &_nav {
            display: none;
        }

        &_btn {
            display: none;
        }

        &_menu {
            display: flex;
        }
    }
}